.conteiner-contact{
  display: grid;
  grid-template-columns: 25% 75%;
}
.page-header {
  color:  #59ce8f;
  margin-bottom: 40px;
  padding: 20px 0;
  text-align: center;
}
.page-contact{
  color:  #59ce8f;
  font-size: 48px;
 

}
.contact-left-box{
  width: 100%%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.title-phonebook{
  font-size: 32px;
  font-weight: bold;
  text-transform: uppercase;
}
.dont-found{
  margin: auto;
  font-size: 32px;
  color:#59ce8f;
  font-weight: 700;
}